<style>
  .info-check {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
  }
  .info-check-table {
    width: 100%;
    clear: both;
    flex: 1;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-right: 15px;
  }

  td.ivu-table-expanded-cell {
    padding-left: 120px;
    text-indent: 2em;
  }

  .table-action {
    margin-bottom: 20px;
  }

  .table-action b {
    font-size: 15px;
    padding-left: 10px;
    margin-right: 20px;
    font-weight: normal;
  }
  .table-action button {
    margin: 0 30px;
  }
</style>

<template>
  <div class="info-check">

    <!---->
    <Breadcrumb>
      <Breadcrumb-item></Breadcrumb-item>
      <Breadcrumb-item></Breadcrumb-item>
    </Breadcrumb>
    <!--/-->

    <!---->
    <div class="search-wrap">
      <Input v-model="search" placeholder="">
        <Select v-model="select" slot="prepend" style="width: 80px">
          <Option value="store_name"></Option>
          <Option value="area"></Option>
          <Option value="store_owner"></Option>
        </Select>
        <Button slot="append" icon="ios-search"></Button>
      </Input>
    </div>
    <!--/-->

    <!---->
    <div class="table-action">
      <b></b>
      <Radio-group v-model="tableSize" type="button">
        <Radio label="large"></Radio>
        <Radio label="default"></Radio>
        <Radio label="small"></Radio>
      </Radio-group>
    </div>
    <div class="info-check-table">
      <Table :columns="columns" :data="stores" :size="tableSize"></Table>
    </div>
    <!--/-->

    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="100" :current="1" @on-change="changePage" show-total></Page>
      </div>
    </div>

  </div>
</template>

<script>
  import expandRow from './expandStoreList.vue'
  export default {
    data() {
      return {
        search: '',
        select: 'store_name',
        tableSize: 'default',
        columns: [
          {
            title: 'ID',
            key: 'id',
            width: 150,
            align: 'center'
          },
          {
            type: 'expand',
            width: 50,
            render: (h, params) => {
              return h(expandRow, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title: '',
            key: 'name',
            align: 'center'
          },
          {
            title: '',
            key: 'area',
            align: 'center',
            sortable: true
          },
          {
            title: '',
            key: 'tel',
            width: 150,
            align: 'center'
          },
          {
            title: '',
            key: 'owner',
            width: 100,
            align: 'center'
          },
          {
            title: '',
            key: 'time',
            align: 'center',
            width: 150,
            sortable: true
          },
          {
            title: '',
            key: 'action',
            width: 120,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  on: {
                  	click: ()=> {
                  		this.goToStore(params.row)
                    }
                  }
                }, '')
              ])
            }
          }
        ],
        stores: [
          {
            id: 1,
            name: '',
            area: '',
            address: '',
            tel: 18456122214,
            time: '2017-03-20',
            deal: 500,
            month: 12,
            year: 50,
            owner: ''
          },
          {
            id: 2,
            name: '',
            area: '',
            address: '',
            tel: 18456122214,
            time: '2017-06-20',
            deal: 20,
            month: 2.1,
            year: 21,
            owner: ''
          },
          {
            id: 3,
            name: '',
            area: '',
            address: '',
            tel: 18456122214,
            time: '2017-11-20',
            deal: 100,
            month: 1.2,
            year: 10,
            owner: ''
          },
          {
            id: 4,
            name: '',
            area: '',
            address: '',
            tel: 18456122214,
            time: '2017-01-19',
            deal: 111,
            month: 6,
            year: 110,
            owner: ''
          },
        ],
        passIds: []
      }
    },
    created(){
    },
    methods: {
      changePage(e){
        console.log(e)
      },

      /**
      * 
      * */
      goToStore(store){
      	this.$router.push({name: 'store_chart', params: {store: store}})
      }
    }
  }
</script>



