      </Col>
      <Col span="8">
        <span class="expand-key"></span>
        <span class="expand-value">{{ row.year }} </span>
      </Col>
    </Row>
    <Row>
      <Col span="24">
        <span class="expand-key"></span>
        <span class="expand-value">{{ row.address }}</span>
      </Col>
    </Row>
  </div>
</template>
<script>
  export default {
    props: {
      row: Object
    }
  };
</script>



