<template>
  <div class="info-check">

    <!---->
    <Breadcrumb>
      <Breadcrumb-item></Breadcrumb-item>
      <Breadcrumb-item></Breadcrumb-item>
    </Breadcrumb>
    <!--/-->

    <!---->
    <div class="search-wrap">
      <Input v-model="search" placeholder="">
        <Select v-model="select" slot="prepend" style="width: 80px">
          <Option value="id">ID</Option>
          <Option value="store"></Option>
          <Option value="commodity"></Option>
        </Select>
        <Button slot="append" icon="ios-search"></Button>
      </Input>
    </div>
    <!--/-->

    <!---->
    <div class="table-action">
      <b></b>
      <Radio-group v-model="tableSize" type="button">
        <Radio label="large"></Radio>
        <Radio label="default"></Radio>
        <Radio label="small"></Radio>
      </Radio-group>
    </div>
    <div class="info-check-table">
      <Table :columns="columns" :data="orderList" :size="tableSize"></Table>
    </div>
    <!--/-->

    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="100" :current="1" @on-change="changePage" show-total></Page>
      </div>
    </div>

  </div>
</template>

<style>
  .info-check {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
  }

  .info-check-table {
    width: 100%;
    clear: both;
    flex: 1;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-right: 15px;
  }

  td.ivu-table-expanded-cell {
    padding-left: 200px;
    text-indent: 2em;
  }

  .table-action {
    margin-bottom: 20px;
  }

  .table-action b {
    font-size: 15px;
    padding-left: 10px;
    margin-right: 20px;
    font-weight: normal;
  }

  .table-action button {
    margin: 0 30px;
  }

  .ivu-table-tbody .table-price span {
    color: #ff963d;
  }

  .ivu-table-tbody .table-price span:after {
    content: '';
    display: inline-block;
    margin-left: 5px;
  }
</style>

<script>
  import expandRow from './expandOrderList.vue'
  export default {
    data() {
      return {
        search: '',
        select: 'store',
        tableSize: 'default',
        columns: [
          {
            title: 'ID',
            key: 'id',
            align: 'center',
            width: 200,
            sortable: true
          },
          {
            type: 'expand',
            width: 50,
            render: (h, params) => {
              return h(expandRow, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title: '',
            key: 'store',
            align: 'center',
            sortable: true
          },
          {
            title: '',
            key: 'commodity',
            align: 'center'
          },
          {
            title: '/()',
            key: 'price',
            width: 150,
            align: 'center',
            sortable: true,
            className: 'table-price'
          },
          {
            title: '',
            key: 'create_time',
            align: 'center',
            width: 250,
            sortable: true
          },
          {
            title: '',
            key: 'action',
            width: 200,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  on: {
                  	click: () => {
                  		this.goToCommodity(params.row)
                    }
                  }
                }, '')
              ])
            }
          }
        ],
        orderList: [
          {
            id: 1,
            commodity: '',
            store: '',
            create_time: '2017-06-20',
            price: '50',
            deal: '5000',
            buy_times: 24
          },
          {
            id: 2,
            commodity: '',
            store: '',
            create_time: '2017-04-11',
            price: '23',
            deal: '5000',
            buy_times: 4
          },
          {
            id: 3,
            commodity: '',
            store: '',
            create_time: '2017-03-20',
            price: '553',
            deal: '15000',
            buy_times: 1
          },
          {
            id: 4,
            commodity: '',
            store: '',
            create_time: '2017-12-20',
            price: '150',
            deal: '500',
            buy_times: 240
          },
        ],
        passIds: []
      }
    },
    created(){
    },
    methods: {
      changePage(e){
        console.log(e)
      },

      /**
      * 
      * */
      goToCommodity(params){
//      	console.log(params)
        this.$router.push({name: 'order_one', params: {order: params}})
      }
    }
  }
</script>



